<template>
  <div class="app-form-container">
    <h1
      class="form__header"
    >
      Formulár
    </h1>
    <b-form class="app-form">
      <app-form-label text="Email" />
      <b-form-input
        type="email"
        placeholder="@"
        v-model="form.email"
        class="form__element form__email"
      />
      <app-form-label text="Stránky, ktorých ponuku sledovať" />
      <b-form-group
        class="form__element"
      >
        <b-form-checkbox-group
          id="providers"
        >
          <!-- TODO: use bootstrap checkbox -->
          <!-- <b-form-checkbox
            :disabled="true"
            class="active"
          >
            Tipsport
          </b-form-checkbox> -->
          <input 
            type="checkbox"
            checked
            disabled
          /> Tipsport
          <!-- <label class="btn btn-default active">
              <input type="checkbox" checked="checked">Tipsport
          </label> -->
        </b-form-checkbox-group>
      </b-form-group>
      <app-form-label text="Športy a súťaže" />
      <b-form-group>
        <b-form-checkbox-group
          class="form__row-group"
        >
          <div
            v-for="sport in sports"
            :key="sport.id"
            class="form__element"
          >
            <app-form-sport-group
              :sport="sport"
              :form="form"
            />
          </div>
        </b-form-checkbox-group>
      </b-form-group>
      <app-form-summary
        :sports="sports"
        :form="form"
      />
      <b-button
        v-if="!formSubmitted"
        v-on:click="submitForm"
        variant="success"
        class="form__button"
      >
        Potvrdiť
      </b-button>
      <div
        v-else
        class="form__success-message"
      >
        Formulár bol úspešne odoslaný. Skontroluj si emailovú schránku.
      </div>
    </b-form>
    <info-box-success 
      :onClose="closeInfoBox"
      :showInfoBox="showSuccess"
    />
    <info-box-warning
      :onClose="hideWarning"
      :message="warningMessage"
    />
  </div>
</template>

<script>

import axios from 'axios';

import config from '../config';

import {
  BForm,
  BFormInput,
  BFormGroup,
  BFormCheckbox,
  BFormCheckboxGroup,
  BButton,
} from 'bootstrap-vue'

import AppFormSportGroup from './AppFormSportGroup'
import AppFormLabel from './AppFormLabel'
import AppFormSummary from './AppFormSummary'
import InfoBoxSuccess from './InfoBoxSuccess'
import InfoBoxWarning from './InfoBoxWarning'

export default {
  name: 'app-form',
  components: {
    BForm,
    BFormInput,
    BFormGroup,
    BFormCheckbox,
    BFormCheckboxGroup,
    BButton,
    AppFormSportGroup,
    AppFormLabel,
    AppFormSummary,
    InfoBoxSuccess,
    InfoBoxWarning,
  },
  data() {
    return {
      sports: [],
      form: {
        email: '',
        sports: [],
        competitions: []
      },
      showSuccess: false,
      formSubmitted: false,
      warningMessage: ''
    }
  },
  async created() {
    try {
      const response = await axios.get(`${config.apiUrl}/api/get-sports`);
      
      this.sports = response.data;
    } catch (err) {
      console.log(err);
      // TODO: notify about error
    }
  },
  methods: {
    submitForm: async function () {
      if (!this.form.email) {
        this.showWarning('Prosím zadaj email.');
        return;
      }
      if (this.form.sports.length === 0 && this.form.competitions.length === 0) {
        this.showWarning('Treba vybrať aspoň jeden šport alebo jednu súťaž.');
        return;
      }

      try {

        this.showSuccess = true;
        this.formSubmitted = true;

        const response = await axios.post(
          `${config.apiUrl}/api/subscription/create`,
          {
            email: this.form.email,
            sportIds: this.form.sports,
            competitionIds: this.form.competitions,
          }
        )
        
        console.log(response.status);
      } catch (err) {
        console.log(err);
        // TODO: notify about error
      }
    },
    closeInfoBox: function () {
      this.showSuccess = false;
    },
    showWarning: function (message) {
      this.warningMessage = message;
    },
    hideWarning: function () {
      this.warningMessage = '';
    }
  }
}
</script>

