<template>
  <div class="form__summary">
    <app-form-label text="Sumár" />
    <div
      class="form__summary__text"
    >
      <div
        class="form__summary__container"
      >
        Na adresu {{ form.email || '...' }} ti príde notifikácia vtedy, keď:
      </div>
      <div
        v-if="followedSports.length > 0"
        class="form__summary__container"
      >
        <div class="form__summary__text__label">
          - pribudne nová súťaž v niektorom zo športov:
        </div>
        <div v-for="sport in followedSports" :key="sport.id">
          {{ sport.slovakName }}
        </div>
      </div>
      <div
        v-if="followedCompetitions.length > 0"
        class="form__summary__container"
      >
        <div class="form__summary__text__label">
          - pribudne nový zápas v niektorej zo súťaží:
        </div>
        <div v-for="comp in followedCompetitions" :key="comp.id">
          {{ comp.name }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import AppFormLabel from './AppFormLabel'

export default {
  name: 'app-form-summary',
  components: {
    AppFormLabel,
  },
  props: {
    sports: Array,
    form: Object
  },
  computed: {
    followedSports: function () {
      return this.sports.filter(
        sport => this.form.sports.includes(sport.id)
      )
    },
    followedCompetitions: function () {
      const followed = [];
      for (let sport of this.sports) {
        followed.push(...sport.competitions.filter(
          competition => this.form.competitions.includes(competition.id)
        ))
      }
      return followed;
    }
  }
}

</script>

<style scoped>

.form__summary__text {
  font-family: 'montserrat';
}

.form__summary__text__label {
  margin-bottom: 0.3rem;
}

.form__summary__container {
  margin-bottom: 1rem;
}

</style>